import styled from "styled-components";
import React, { useEffect, useState } from "react";
import { AnimatePresence, motion } from "framer-motion";
import my from "./img/my.jpg"

const Wrapper = styled.div`
    width: 100vw;
    height: 100vh;
    background-color: #222222; 
    display: flex;
    align-items: center;
`

const Container = styled.div`
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
`

const Info = styled.div`
    width: 40%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    border-right: 1px solid #ffffff;
`

const Img = styled.div`
    width: 300px;
    height: 300px;
    border-radius: 150px;
    background-color: #ffffff;
    background-position: center;
    background-size: cover;
    margin-bottom: 30px;
`

const Name= styled.div`
    font-size: 35px;
    margin-bottom: 30px;

    & > div:nth-child(2){
        width: 50px;
        border-bottom: 2px solid #ffffff;
        margin-top: 10px;
    }
`

const Etc = styled.div`
    font-size: 20px;
    margin-bottom: 30px;
`

const Answer = styled.div`
    width: 60%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
`

const List = styled(motion.div)`
    width: 80%;
    border-radius: 15px;
    background-color: #6C6C6C;
    font-size: 23px;
    padding: 40px 20px;
    cursor: pointer;
`

const Qna = styled(motion.div)`
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
`

const ListQna= styled(motion.div)`
    width: 800px;
    height: 400px;
    background-color: #333333;
    border-radius: 15px;
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0;

    & > div:nth-child(1){
        width: 100%;
        font-size: 35px;
        margin-bottom: 15px;
    }

    & > div{
        width: 80%;
        font-size: 20px;
        margin: 0;
        margin-bottom: 10px;
    }
`

function Myinfo(){
    const [selectId, setSelectId] = useState(null);

    return(
        <Wrapper>
            <Container>

            
                <Info>
                    <Img style={{"backgroundImage" : `url(${my})`}} />
                    <Name>
                        <div></div>
                        <div />
                    </Name>
                    <Etc>1999. 11. 03</Etc>
                    <Etc>010 - 9357 - 4873</Etc>
                    <Etc> IT</Etc>
                </Info>
                <Answer>
                        <List layoutId="A" onClick={()=>setSelectId("A")}>#  </List>
                        <List layoutId="B" onClick={()=>setSelectId("B")}>#    </List>
                        <List layoutId="C" onClick={()=>setSelectId("C")}>#   </List>
                </Answer>
                <AnimatePresence>
                    

                    {selectId && 
                    <Qna>
                        {selectId === 'A' && <ListQna layoutId="A" onClick={()=>setSelectId(null)} >
                            <div>#  </div>
                            <div>   1   .</div>
                            <div>              .</div>
                            <div>    .         </div>
                            <div>               .</div>   
                        </ListQna>}
                        {selectId === 'B' && <ListQna layoutId="B" onClick={()=>setSelectId(null)} >
                            <div>#    </div>  
                            <div>        .</div>  
                            <div>   HTML/CSS        .</div>
                            <div>                 .</div>
                        </ListQna>}
                        {selectId === 'C' && <ListQna layoutId="C" onClick={()=>setSelectId(null)} >
                            <div>#   </div>
                            <div>        </div>
                            <div>          PM   </div>
                            <div>  , ,         PM  .</div>
                        </ListQna>}

                    </Qna>}
                </AnimatePresence>
            
            </Container>
        </Wrapper>
    )
}

export default Myinfo;